import { switchMap, filter, take, mapTo, startWith, distinctUntilChanged } from 'rxjs/operators';
import { Observable, merge } from 'rxjs';
import { Injectable, Inject, NgZone } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { mouseMoveFrom, touchEndFrom, mouseEnterFrom, mouseLeaveFrom } from '../observables/fromAliases';
import { getActualTarget } from '../utils/dom/getActualTarget';
import { zoneFree } from '../observables/zoneFree';

@Injectable({
    providedIn: 'root',
})
export class HoveredService {
    private readonly documentEvents$: Observable<Event>;

    constructor(
        @Inject(DOCUMENT) documentRef: Document,
        @Inject(NgZone) private readonly ngZone: NgZone,
    ) {
        this.documentEvents$ = merge(
            mouseMoveFrom(documentRef),
            touchEndFrom(documentRef),
        );
    }

    createHovered$(
        target: Element,
        options: AddEventListenerOptions = {},
    ): Observable<boolean> {
        return mouseEnterFrom(target, options).pipe(
            switchMap(() =>
                merge(
                    mouseLeaveFrom(target, options),
                    this.documentEvents$.pipe(
                        filter(event => !target.contains(getActualTarget(event))),
                        take(1),
                    )
                ).pipe(
                    mapTo(false),
                    startWith(true),
                ),
            ),
            zoneFree(this.ngZone),
            distinctUntilChanged(),
        );
    }
}
