import { Subject } from 'rxjs';
import { Mode } from '../../enums/mode';
import { Directive, Input, forwardRef } from '@angular/core';
import { CHANGE_EMITTERS } from '../../tokens/changeEmitters';
import { ModeVariants } from '../../types/modeVariants';
import { ASSERT } from '../../../../angular-cdk/src/classes/assert';

@Directive({
    selector: `[mode]`,
    providers: [
        {
            provide: CHANGE_EMITTERS,
            useExisting: forwardRef(() => ModeDirective),
            multi: true,
        },
    ],
})
export class ModeDirective {
    @Input('mode')
    set mode(mode: ModeVariants) {
        ASSERT.assert(
            mode !== Mode.TABLE,
            'mode="table" is deprecated, use table instead',
        );

        this.currentMode = mode;
        this.change$.next();
    }

    get mode(): ModeVariants | null {
        return this.currentMode;
    }

    change$ = new Subject<void>();

    private currentMode: ModeVariants | null = null;
}
