import { EventWith } from '../types/eventWith';
import { FromEventTarget, fromEvent } from 'rxjs/internal/observable/fromEvent';
import { Observable } from 'rxjs';

export function clickFrom<T extends FromEventTarget<EventWith<MouseEvent, T>>>(
    target: Exclude<T, null>,
    options: AddEventListenerOptions = {},
): Observable<EventWith<MouseEvent, T>> {
    return fromEvent(target, 'click', options);
}

