import { takeUntil } from 'rxjs/operators';
import { FormControl, ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { BehaviorSubject, Subject } from 'rxjs';
import { Component, OnInit, ChangeDetectionStrategy, OnDestroy, Input, forwardRef } from '@angular/core';

@Component({
    selector: 'app-preview-property-input',
    templateUrl: './preview-property-input.component.html',
    styleUrls: ['./preview-property-input.component.less'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => PreviewPropertyInputComponent),
            multi: true,
        }
    ],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PreviewPropertyInputComponent implements ControlValueAccessor, OnInit, OnDestroy {
    @Input()
    inputData: object;

    public readonly formControl: FormControl = new FormControl();
    public readonly disabled$: BehaviorSubject<boolean> = new BehaviorSubject(false);

    private readonly componentDestroyed$: Subject<void> = new Subject();

    ngOnInit(): void {
        this.formControl.valueChanges.pipe(
            takeUntil(this.componentDestroyed$),
        ).subscribe(
            (value: any) => this.onChange(value),
        );
    }

    ngOnDestroy(): void {
        this.componentDestroyed$.next();
    }

    public writeValue(value: any): void {
        this.formControl.setValue(value, { emitEvent: false });
    }

    public setDisabledState(disabled: boolean): void {
        if (disabled) {
            this.formControl.disable({ emitEvent: false });
        } else {
            this.formControl.enable({ emitEvent: false });
        }
    }

    public registerOnChange(callback: (value: any) => void): void {
        this.onChange = callback;
    }

    public registerOnTouched(callback: () => void): void {
        this.onTouched = callback;
    }

    private onChange = (value: any): void => { };
    private onTouched = (): void => { };

}
