import { Observable } from 'rxjs';
import { FocusVisibleService } from '../../services/focusVisible.service';
import { DestroyService } from '../../services/destroy.service';
import { Directive, Output, Inject } from '@angular/core';

@Directive({
    // tslint:disable-next-line: directive-selector
    selector: '[focusVisibleChange]',
    providers: [DestroyService, FocusVisibleService],
})
export class FocusVisibleDirective {
    @Output()
    readonly focusVisibleChange: Observable<boolean>;

    constructor(@Inject(FocusVisibleService) focusVisible$: FocusVisibleService) {
        this.focusVisibleChange = focusVisible$;
    }
}
