import { Component, OnInit, ChangeDetectionStrategy, Inject, OnDestroy } from '@angular/core';
import { PreviewComponentClass } from '../../types/preview-component-class.type';
import { PropertyInputInterface, PropertyOutputInterface } from '../../interfaces/properties.interface';
import { FormGroup, FormControl } from '@angular/forms';
import { BehaviorSubject, Subject } from 'rxjs';
import { PREVIEW_CONFIG_INJECTION_TOKEN } from '../../tokens/preview-config.token';
import { PreviewConfigInterface } from '../../interfaces/preview-config.interface';
import { takeUntil } from 'rxjs/operators';

@Component({
    selector: 'app-properties-page',
    templateUrl: './properties-page.component.html',
    styleUrls: ['./properties-page.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PropertiesPageComponent implements OnInit, OnDestroy {
    public readonly component: PreviewComponentClass<Component>;
    public readonly inputs: PropertyInputInterface<{}>[];
    public readonly outputs: PropertyOutputInterface<{}>[];
    public readonly formGroup: FormGroup;
    public readonly inputValues$: BehaviorSubject<Record<string, any>> = new BehaviorSubject({});

    private readonly componentDestroyed$: Subject<void> = new Subject();

    constructor(
        @Inject(PREVIEW_CONFIG_INJECTION_TOKEN) config: PreviewConfigInterface<Component>
    ) {
        this.component = config.properties.customComponent || config.componentPreview;
        this.inputs = [
            ...config.properties.inputs,
            ...(config.properties.customInputs || []),
        ];
        this.outputs = config.properties.outputs;
        this.formGroup = this.getFormGroup();
    }

    ngOnInit(): void {
        this.formGroup.valueChanges.pipe(
            takeUntil(this.componentDestroyed$),
        ).subscribe((values: Record<string, any>) => this.inputValues$.next(values));
    }

    ngOnDestroy(): void {
        this.componentDestroyed$.next();
    }

    private getFormGroup(): FormGroup {
        const formGroupData: { [key: string]: FormControl } = {};

        for (const input of this.inputs) {
            formGroupData[input.selector] = new FormControl();
        }

        return new FormGroup(formGroupData);
    }
}
