import { ActivatedRoute } from '@angular/router';
import { PreviewComponentClass } from './types/preview-component-class.type';
import { PreviewConfigInterface } from './interfaces/preview-config.interface';
import { PREVIEW_CONFIG_INJECTION_TOKEN } from './tokens/preview-config.token';
import { Component, OnInit, ChangeDetectionStrategy, Inject } from '@angular/core';
import { MuiTabInterface } from 'src/app/shared/interfaces/mui-tab.interface';

const PREVIEW_PAGES = {
    DESCRIPTION: 'description',
    PROPERTIES: 'properties',
    CONNECTION: 'connection',
}

@Component({
    selector: 'app-preview',
    templateUrl: './preview.component.html',
    styleUrls: ['./preview.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PreviewComponent implements OnInit {

    public readonly menuItems: MuiTabInterface[];

    public title: string;
    public activatedItemIndex = 0;

    constructor(
        @Inject(PREVIEW_CONFIG_INJECTION_TOKEN) private config: PreviewConfigInterface<PreviewComponentClass<{}>>,
        private activatedRoute: ActivatedRoute,
    ) {
        this.menuItems = this.getMenuItems();
    }

    ngOnInit(): void {
        this.title = this.activatedRoute.snapshot.data.title || 'Title Error';
    }

    private getMenuItems(): MuiTabInterface[] {
        const result: MuiTabInterface[] = [
            {
                caption: '',
                routerLink: PREVIEW_PAGES.DESCRIPTION,
            }
        ];

        if (this.config.properties) {
            result.push({
                caption: '',
                routerLink: PREVIEW_PAGES.PROPERTIES,
            });
        }

        if (this.config.connection) {
            result.push({
                caption: ' ',
                routerLink: PREVIEW_PAGES.CONNECTION,
            });
        }

        return result;
    }

}
