import { MonoTypeOperatorFunction, Operator, Observable, TeardownLogic, Subscriber } from 'rxjs';
import { NgZone } from '@angular/core';

class ZoneFreeSubscriber<T> extends Subscriber<T> {
    constructor(destination: Subscriber<T>, private readonly ngZone: NgZone | null) {
        super(destination);
    }

    protected _next(value: T) {
        this.runInOptionslZone(() => {
            super._next(value);
        });
    }

    protected _error(err: any) {
        this.runInOptionslZone(() => {
            super._error(err);
        });
    }

    protected _complete() {
        this.runInOptionslZone(() => {
            super._complete();
        });
    }

    private runInOptionslZone(handler: () => void) {
        if (this.ngZone === null) {
            handler();

            return;
        }

        this.ngZone.run(() => {
            handler();
        });
    }
}

export class ZoneFreeOperator<T> implements Operator<T, T> {
    constructor(private readonly ngZone: NgZone) { }

    call(subscriber: Subscriber<T>, source$: Observable<T>): TeardownLogic {
        const ngZone: NgZone | null = NgZone.isInAngularZone() ? this.ngZone : null;

        return this.ngZone.runOutsideAngular(() =>
            source$.subscribe(new ZoneFreeSubscriber(subscriber, ngZone)),
        );
    }
}

export function zoneFree<T>(ngZone: NgZone): MonoTypeOperatorFunction<T> {
    return source$ => source$.lift(new ZoneFreeOperator<T>(ngZone));
}
