import { Directive, Input, Inject, TemplateRef } from '@angular/core';
import { AbstractInteractiveGroupItemConnector } from 'projects/angular-cdk/src/abstract/interactiveGroupItemConnector';
import { RouterLinkOptions } from 'projects/angular-cdk/src/interfaces/routerLinkOptions';
import { RouterLinkActiveOptions } from 'projects/angular-cdk/src/interfaces/routerLinkActiveOptions';

@Directive({
    selector: '[tabGroupItem]',
})
export class TabGroupItemConnectorDirective extends AbstractInteractiveGroupItemConnector {
    @Input()
    tabGroupItem: string | null = null;

    @Input('tabGroupItemDisabled')
    disabled = false;

    @Input('tabGroupItemOptions')
    routerLinkOptions: RouterLinkOptions = {};

    @Input('tabGroupItemActiveOptions')
    routerLinkActiveOptions: RouterLinkActiveOptions = {
        exact: false,
    };

    constructor(
        @Inject(TemplateRef) readonly template: TemplateRef<{}>,
    ) {
        super(template);
    }

    get routerLink(): string | null {
        return this.tabGroupItem === '' ? null : this.tabGroupItem;
    }
}
