import { PreviewConfigInterface } from '../../interfaces/preview-config.interface';
import { PREVIEW_CONFIG_INJECTION_TOKEN } from '../../tokens/preview-config.token';
import { Component, ChangeDetectionStrategy, Inject } from '@angular/core';
import { CodePreviewInterface } from 'src/app/shared/interfaces/code-preview.interface';

@Component({
    selector: 'app-connection-page',
    templateUrl: './connection-page.component.html',
    styleUrls: ['./connection-page.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConnectionPageComponent {

    public readonly moduleName: string;
    public readonly codeElements: {
        codePreview: CodePreviewInterface;
        fileName?: string;
    }[];
    public readonly customText: string[];

    constructor(
        @Inject(PREVIEW_CONFIG_INJECTION_TOKEN) config: PreviewConfigInterface<Component>
    ) {
        this.moduleName = config.connection.moduleName;
        this.codeElements = config.connection.codeElements;
        this.customText = config.connection.customText;
    }
}
