import { PreviewComponentClass } from '../../types/preview-component-class.type';
import { PreviewExampleType } from '../../types/preview-example.type';
import { Component, ChangeDetectionStrategy, Input, OnChanges } from '@angular/core';
import { PreviewProperties } from '../../types/preview-properties.type';

@Component({
    selector: 'app-preview-example-page',
    templateUrl: './preview-example-page.component.html',
    styleUrls: ['./preview-example-page.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PreviewExamplePageComponent implements OnChanges {

    @Input()
    example: PreviewExampleType<{}>;

    @Input()
    defaultComponent: PreviewComponentClass<{}>;

    public typeDefault: boolean;
    public component: object;
    public config: PreviewProperties<{}>;

    public activeItemIndex = 0;

    ngOnChanges(): void {
        this.typeDefault = !('previewComponent' in this.example);
        this.component = 'previewComponent' in this.example ? this.example.previewComponent : null;
        this.config = 'config' in this.example ? this.example.config : {};
    }

}
