
import { RouterModule } from '@angular/router';
import { PreviewConfigInterface } from './interfaces/preview-config.interface';
import { PREVIEW_CONFIG_INJECTION_TOKEN } from './tokens/preview-config.token';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DescriptionPageComponent } from './components/description-page/description-page.component';
import { ConnectionPageComponent } from './components/connection-page/connection-page.component';
import { PropertiesPageComponent } from './components/properties-page/properties-page.component';
import { PreviewComponent } from './preview.component';
import { PreviewExamplePageComponent } from './components/preview-example-page/preview-example-page.component';
import { PreviewComponentBuilderComponent } from './components/preview-component-builder/preview-component-builder.component';
import { PreviewExampleComponentBuilderComponent } from './components/preview-example-component-builder/preview-example-component-builder.component';
import { PreviewPropertyInputComponent } from './components/preview-property-input/preview-property-input.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { MuiCodePreviewModule } from 'src/app/library/mui-flex-library/components/codePreview/codePreview.module';
import { MuiCardBoxModule } from 'src/app/library/mui-flex-library/components/mui-card-box/mui-card-box.module';
import { MuiTabsGroupModule } from 'src/app/library/mui-flex-library/components/mui-tabs-group/mui-tabs-group.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';


@NgModule({
    declarations: [
        DescriptionPageComponent,
        ConnectionPageComponent,
        PropertiesPageComponent,
        PreviewComponent,
        PreviewExamplePageComponent,
        PreviewComponentBuilderComponent,
        PreviewExampleComponentBuilderComponent,
        PreviewPropertyInputComponent,
    ],
    imports: [
        CommonModule,
        MuiTabsGroupModule,
        MuiCardBoxModule,
        MuiCodePreviewModule,
        SharedModule,
        FormsModule,
        ReactiveFormsModule,
        RouterModule,
    ]
})
export class PreviewModule {
    static forRoot<T>(config: PreviewConfigInterface<T>): ModuleWithProviders[] {
        return [
            {
                ngModule: PreviewModule,
                providers: [
                    {
                        provide: PREVIEW_CONFIG_INJECTION_TOKEN,
                        useValue: config,
                    },
                ],
            },
            RouterModule.forChild([
                {
                    path: '',
                    component: PreviewComponent,
                    children: [
                        {
                            path: 'description',
                            component: DescriptionPageComponent,
                        },
                        ...(config.properties ? [{
                            path: 'properties',
                            component: PropertiesPageComponent,
                        }] : []),
                        ...(config.connection ? [{
                            path: 'connection',
                            component: ConnectionPageComponent,
                        }] : []),
                        {
                            path: '',
                            redirectTo: 'description',
                            pathMatch: 'full',
                        },
                    ],
                },
            ]),
        ];
    }
}
