import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MuiCodePreviewComponent } from './codePreview.component';
import { HighlightModule, HIGHLIGHT_OPTIONS } from 'ngx-highlightjs';
import { MuiIconModule } from 'projects/angular-kit/src/public-api';


@NgModule({
    declarations: [
        MuiCodePreviewComponent,
    ],
    imports: [
        CommonModule,
        HighlightModule,
        MuiIconModule,
    ],
    providers: [
        {
            provide: HIGHLIGHT_OPTIONS,
            useValue: {
                config: {
                    tabReplace: '2',
                },
            }
        }
    ],
    exports: [
        MuiCodePreviewComponent,
    ],
})
export class MuiCodePreviewModule { }
