import { takeUntil } from 'rxjs/operators';
import { DestroyService } from './destroy.service';
import { Observable } from 'rxjs';
import { Injectable, Inject, ElementRef, ChangeDetectorRef } from '@angular/core';
import { focusVisibleObservable } from '../observables/focusVisibleObservable';
import { watch } from '../observables/watch';

@Injectable()
export class FocusVisibleService extends Observable<boolean> {
    private readonly focusVisible$: Observable<boolean>;

    constructor(
        @Inject(ElementRef) { nativeElement }: ElementRef<Element>,
        @Inject(ChangeDetectorRef) changeDetectorRef: ChangeDetectorRef,
        @Inject(DestroyService) destroy$: DestroyService,
    ) {
        super(subscriber => this.focusVisible$.subscribe(subscriber));

        this.focusVisible$ = focusVisibleObservable(nativeElement).pipe(
            watch(changeDetectorRef),
            takeUntil(destroy$),
        );
    }
}
