import { isDevMode } from '@angular/core';
import { AssertLevel } from '../enums/assertLevel';

const NOOP = () => { };

export class AssertHelper {
    bootstrapped = false;

    level = AssertLevel.WARN;

    get assert(): (assertion: boolean, ...args: any[]) => void {
        if (!this.bootstrapped || !isDevMode()) {

            return NOOP;
        }

        return Function.prototype.bind.call(console.assert, console);
    }

    get assertWarn(): (assertion: boolean, ...args: any[]) => void {
        if (!this.bootstrapped || this.level < AssertLevel.WARN || !isDevMode()) {
            return NOOP;
        }

        return Function.prototype.bind.call(console.assert, console);
    }
}

export const ASSERT = new AssertHelper();
