import { TabGroupItemConnectorDirective } from './tab-group-item-connector.directive';
import { AbstractInteractiveGroup } from 'projects/angular-cdk/src/abstract/interactiveGroup';
import { Component, ChangeDetectionStrategy, AfterViewInit, AfterViewChecked, Inject, ChangeDetectorRef, ElementRef, Input, ContentChildren, QueryList, ViewChildren, ViewChild, Renderer2, HostBinding, NgZone, Optional, forwardRef } from '@angular/core';
import { ANIMATION_DURATION } from 'projects/angular-core/src/constants/animationDuration';
import { getOriginalArrayFromQueryList } from 'projects/angular-cdk/src/utils/miscellaneous/getOriginalArrayFromQueryList';
import { SizeS } from 'projects/angular-core/src/types/size';
import { RouterLinkActive, Router, NavigationEnd } from '@angular/router';
import { isNativeFocusedIn } from 'projects/angular-cdk/src/utils/focus/isNativeFocusedIn';
import { blurNativeFocused } from 'projects/angular-cdk/src/utils/focus/blurNativeFocused';
import { DOCUMENT } from '@angular/common';
import { filter, delay, takeUntil } from 'rxjs/operators';
import { POLLING_TIME } from 'projects/angular-cdk/src/constants/pollingTime';
import { ModeVariants } from 'projects/angular-core/src/types/modeVariants';
import { ModeDirective } from 'projects/angular-core/src/directives/mode/mode.directive';
import { AbstractInteractiveGroupItemConnector } from 'projects/angular-cdk/src/abstract/interactiveGroupItemConnector';
import { DestroyService } from 'projects/angular-cdk/src/services/destroy.service';
import { FOCUSABLE_ITEM_ACCESSOR } from 'projects/angular-cdk/src/tokens/focusableItemAccessor';

const MORE_WIDTH = 50;
const MARGIN = 24;
const MARGIN_SMALL = 16;

@Component({
    selector: 'tab-group',
    templateUrl: './tab-group.template.html',
    styleUrls: ['./tab-group.style.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    providers: [
        DestroyService,
        {
            provide: FOCUSABLE_ITEM_ACCESSOR,
            useExisting: forwardRef(() => TabGroupComponent)
        }
    ]
})
export class TabGroupComponent extends AbstractInteractiveGroup implements AfterViewInit, AfterViewChecked {
    @Input()
    @HostBinding('class._underline')
    underline = true;

    @Input()
    allowResize = false;

    @Input()
    @HostBinding('attr.data-host-size')
    size: SizeS = 'm';

    @ContentChildren(TabGroupItemConnectorDirective)
    readonly itemsConnectors?: QueryList<TabGroupItemConnectorDirective>;

    lastVisibleIndex = Infinity;

    @ViewChildren('focusableElement')
    protected readonly focusableElements?: QueryList<ElementRef<HTMLElement>>;

    @ViewChildren('item')
    protected readonly items?: QueryList<ElementRef<HTMLElement>>;

    @ViewChildren('links')
    private readonly links?: QueryList<RouterLinkActive>;

    @ViewChild('scrollbar', { static: true })
    private scrollbar?: ElementRef<HTMLElement>;

    @ViewChild('underlineEl', { static: true })
    private underlineEl?: ElementRef<HTMLDivElement>;

    public opened = false;
    constructor(
        @Inject(ChangeDetectorRef) changeDetectorRef: ChangeDetectorRef,
        @Inject(ElementRef) elementRef: ElementRef<HTMLElement>,
        @Inject(Renderer2) private readonly renderer: Renderer2,
        @Inject(DOCUMENT) private readonly documentRef: Document,
        @Inject(NgZone) private readonly ngZone: NgZone,
        @Inject(DestroyService) destroy$: DestroyService,
        @Optional()
        @Inject(Router)
        router: Router,
        @Optional()
        @Inject(ModeDirective)
        private readonly modeDirective: ModeDirective | null,
    ) {
        super(changeDetectorRef, elementRef);

        if (!router) {

            return;
        }

        router.events
            .pipe(
                filter(event => event instanceof NavigationEnd),
                delay(POLLING_TIME),
                takeUntil(destroy$),
            ).subscribe(() => {
                this.checkRouterLinks();
            });
    }

    ngAfterViewInit(): void {
        if (!this.itemsConnectors) {
            return;
        }

        this.itemsConnectors.changes.subscribe(() => {
            const itemConnectors = getOriginalArrayFromQueryList(this.itemsConnectors!);

            if (isNativeFocusedIn(this.elementRef.nativeElement)) {
                blurNativeFocused(this.documentRef);
            }

            if (this.activeItemIndex !== null && !itemConnectors[this.activeItemIndex]) {
                this.updateActiveItemIndex(this.activeItemIndex - 1);
                this.setUnderlinePosition();
            }
        });
    }

    ngAfterViewChecked(): void {
        this.checkRouterLinks();
        this.ngZone.runOutsideAngular(() => {
            setTimeout(() => {
                this.setUnderlinePosition();
            });
        });
    }

    get isMoreItemsFocusable(): boolean {
        return this.focusableItemIndex === this.lastVisibleIndex + 1;
    }

    get isMoreItemsExist(): boolean {
        return (
            !!this.itemsConnectors &&
            this.lastVisibleIndex < this.itemsConnectors.length - 1
        );
    }

    get isMoreItemsActive(): boolean {
        return (
            (!!this.activeItemIndex && this.activeItemIndex > this.lastVisibleIndex) ||
            this.isActiveRouteHidden
        );
    }

    get isActiveRouteHidden(): boolean {
        return (
            !!this.links &&
            this.links.some(
                item =>
                    item.isActive &&
                    !!this.itemsConnectors &&
                    this.itemsConnectors.some(
                        (connector, index) =>
                            connector.routerLinkActiveOptions ===
                            item.routerLinkActiveOptions &&
                            index > this.lastVisibleIndex,
                    ),
            )
        );
    }

    get isMoreItemsHovered(): boolean {
        return this.hoveredItemIndex === this.lastVisibleIndex + 1;
    }

    get isMoreItemsPressed(): boolean {
        return this.pressedItemIndex === this.lastVisibleIndex + 1;
    }

    @HostBinding('attr.data-host-mode')
    get hostMode(): ModeVariants | null {
        return this.modeDirective ? this.modeDirective.mode : null;
    }

    public moreItemIsActive(connector: TabGroupItemConnectorDirective): boolean {
        return (
            !!this.itemsConnectors &&
            getOriginalArrayFromQueryList(this.itemsConnectors).indexOf(connector) ===
            this.activeItemIndex
        );
    }

    public itemIsHidden(index: number): boolean {
        return index > this.lastVisibleIndex;
    }

    public onFocusVisible(focusVisible: boolean, itemIndex: number): void {
        if (!focusVisible && itemIndex !== this.focusableItemIndex) {

            return;
        }

        this.updateFocusVisible(focusVisible);
    }

    public onItemClick(
        event: Event,
        itemConnector: TabGroupItemConnectorDirective,
        itemIndex: number,
    ): void {
        event.preventDefault();
        this.tryChangeActiveItemIndex(itemConnector, itemIndex);

        // if (this.scrollbar && this.scrollbar.browserScrollRef && this.focusableElements) {
        //     this.scrollToElement(
        //         this.focusableElements.toArray()[itemIndex].nativeElement,
        //         this.scrollbar.browserScrollRef!.nativeElement,
        //     );
        // }
    }

    onHiddenItemClick(item: TabGroupItemConnectorDirective, index: number) {
        this.tryChangeActiveItemIndex(item, this.lastVisibleIndex + index);
        this.close();
    }

    onItemHovered(hovered: boolean, itemIndex: number) {
        this.updateHoveredItemIndex(hovered, itemIndex);
    }

    onItemPressed(pressed: boolean, itemIndex: number) {
        this.updatePressedItemIndex(pressed, itemIndex);
    }

    onItemKeyDownArrowLeft(event: Event, itemIndex: number) {
        this.moveFocusToPrevItem(itemIndex, event);
    }

    onItemKeyDownArrowRight(event: Event, itemIndex: number) {
        this.moveFocusToNextItem(itemIndex, event);
    }

    public onItemKeyDownEnter(
        event: Event,
        itemConnector: TabGroupItemConnectorDirective,
        itemIndex: number,
    ): void {
        event.preventDefault();

        this.tryChangeActiveItemIndex(itemConnector, itemIndex);
    }

    public onMoreItemKeyDownEnter(): void {
        this.opened = !this.opened;
    }

    public onLinkEnter(event: KeyboardEvent, element: HTMLElement): void {
        event.preventDefault();
        element.click();
    }

    protected setUnderlinePosition() {
        if (!this.underline || !this.underlineEl || !this.items) {
            return;
        }

        if (this.activeItemIndex === null) {
            this.renderer.setStyle(
                this.underlineEl.nativeElement,
                'transform',
                'scale(0.01)',
            );
            this.renderer.setStyle(this.underlineEl.nativeElement, 'width', '0');

            return;
        }

        if (this.underlineEl.nativeElement.offsetWidth) {
            this.renderer.setStyle(
                this.underlineEl.nativeElement,
                'transitionDuration',
                ANIMATION_DURATION,
            );
        }

        const activeElRef = getOriginalArrayFromQueryList(this.items)[this.activeItemIndex];

        if (!activeElRef) {

            return;
        }

        const activeEl = activeElRef.nativeElement;
        const { offsetWidth, offsetLeft } = activeEl;

        this.renderer.setStyle(
            this.underlineEl.nativeElement,
            'transform',
            `translate3d(${offsetLeft}px, 0px, 0px)`,
        );

        this.renderer.setStyle(
            this.underlineEl.nativeElement,
            'width',
            `${offsetWidth}px`,
        );
    }

    protected moveFocusToNextItem(itemIndex: number, event: Event) {
        event.preventDefault();
        this.updateFocusVisible(true);

        if (!this.itemsConnectors) {

            return;
        }

        const array: ReadonlyArray<
            AbstractInteractiveGroupItemConnector
        > = getOriginalArrayFromQueryList<AbstractInteractiveGroupItemConnector>(
            this.itemsConnectors,
        );
        let newIndex = itemIndex + 1;

        const visibleArrayLength = this.isMoreItemsExist
            ? this.lastVisibleIndex + 2
            : array.length;

        do {
            if (newIndex >= visibleArrayLength) {
                newIndex = 0;
            } else if (array[newIndex].disabled) {
                newIndex++;
            } else {
                this.updateFocusableItemIndex(newIndex);
                break;
            }
        } while (itemIndex !== newIndex);
    }

    protected moveFocusToPrevItem(itemIndex: number, event: Event) {
        event.preventDefault();
        this.updateFocusVisible(true);

        if (!this.itemsConnectors) {

            return;
        }

        const array: ReadonlyArray<
            AbstractInteractiveGroupItemConnector
        > = getOriginalArrayFromQueryList<AbstractInteractiveGroupItemConnector>(
            this.itemsConnectors,
        );

        let newIndex = itemIndex - 1;

        const visibleArrayLength = this.isMoreItemsExist
            ? this.lastVisibleIndex + 1
            : array.length - 1;

        do {
            if (newIndex < 0) {
                newIndex = visibleArrayLength;
            } else if (array[newIndex].disabled) {
                newIndex--;
            } else {
                this.updateFocusableItemIndex(newIndex);
                break;
            }
        } while (itemIndex !== newIndex);
    }

    protected updateLastVisibleIndex() {
        if (!this.focusableElements) {

            return;
        }

        if (!this.allowResize) {
            this.setLastVisibleIndex(this.itemsLimit - 1);

            return;
        }

        const margin = this.size === 'm' ? MARGIN : MARGIN_SMALL;
        const width = this.elementRef.nativeElement.clientWidth - MORE_WIDTH;
        let accumulatedWidth = 0;
        let lastVisibleIndex = 0;

        this.focusableElements.some(({ nativeElement }, index, array) => {
            accumulatedWidth +=
                array.length - 1 === index
                    ? nativeElement.clientWidth - MORE_WIDTH - margin
                    : nativeElement.clientWidth;

            const isExceeding = accumulatedWidth > width || index > this.itemsLimit;

            if (!isExceeding) {
                lastVisibleIndex = index;
            }

            return isExceeding;
        });

        this.setLastVisibleIndex(
            this.itemsConnectors && lastVisibleIndex === this.itemsConnectors.length - 1
                ? Infinity
                : lastVisibleIndex - 1,
        );
    }

    private setLastVisibleIndex(lastVisibleIndex: number): void {
        this.lastVisibleIndex = lastVisibleIndex;

        if (!this.isMoreItemsExist) {
            this.opened = false;
        }

        this.changeDetectorRef.detectChanges();
    }

    private close(): void {
        this.opened = false;
        this.focusableItemIndex = this.lastVisibleIndex + 1;
    }

    private checkRouterLinks(): void {
        if (
            !this.links ||
            this.links.length === 0 ||
            !this.itemsConnectors ||
            !this.focusableElements
        ) {
            return;
        }

        const activeLink = this.links.find(link => link.isActive);
        const focusableElements = getOriginalArrayFromQueryList(this.focusableElements);
        const activeElement =
            this.activeItemIndex && focusableElements[this.activeItemIndex];
        const isLink =
            !!activeElement && activeElement.nativeElement.tagName.toUpperCase() === 'A';

        if (!activeLink) {

            if (!isLink) {
                return;
            }

            setTimeout(() => {
                this.updateActiveItemIndex(null);
                this.setUnderlinePosition();
            });

            return;
        }

        const itemsConnectors = getOriginalArrayFromQueryList(this.itemsConnectors);
        const activeIndex = itemsConnectors.findIndex(
            connector =>
                connector.routerLinkActiveOptions === activeLink.routerLinkActiveOptions,
        );

        if (activeIndex !== -1 && activeIndex !== this.activeItemIndex) {
            setTimeout(() => {
                this.updateActiveItemIndex(activeIndex);
                this.setUnderlinePosition();
            });
        }
    }
}
