import { PreviewComponentClass } from '../../types/preview-component-class.type';
import { PreviewExampleType } from '../../types/preview-example.type';
import { PreviewConfigInterface } from '../../interfaces/preview-config.interface';
import { PREVIEW_CONFIG_INJECTION_TOKEN } from '../../tokens/preview-config.token';
import { Component, ChangeDetectionStrategy, Inject } from '@angular/core';

@Component({
    selector: 'app-description-page',
    templateUrl: './description-page.component.html',
    styleUrls: ['./description-page.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DescriptionPageComponent {

    public readonly examples: PreviewExampleType<{}>[];
    public readonly defaultComponent: PreviewComponentClass<{}>;

    constructor(
        @Inject(PREVIEW_CONFIG_INJECTION_TOKEN) config: PreviewConfigInterface<{}>
    ) {
        this.examples = config.description.examples;
        this.defaultComponent = config.componentPreview;
    }


}
