import { Subject } from 'rxjs';
import { AfterContentInit, OnDestroy, Input, HostBinding, Output, EventEmitter, QueryList, ChangeDetectorRef, ElementRef, HostListener } from '@angular/core';
import { AbstractInteractive } from './interactive';
import { AbstractInteractiveGroupItemConnector } from './interactiveGroupItemConnector';
import { startWith, distinctUntilChanged, skip } from 'rxjs/operators';
import { getOriginalArrayFromQueryList } from '../utils/miscellaneous/getOriginalArrayFromQueryList';
import { isNativeFocusedIn } from '../utils/focus/isNativeFocusedIn';
import { EventWith } from '../types/eventWith';
import { setNativeFocused } from '../utils/focus/setNativeFocused';
import { NativeFocusableElement } from '../interfaces/nativeFocusableElement';

export abstract class AbstractInteractiveGroup extends AbstractInteractive implements AfterContentInit, OnDestroy {
    @Input()
    activeItemIndex: number | null = null;

    @Input()
    focusableItemIndex = 0;

    @Input()
    disabled = false;

    @Input()
    itemsLimit = Infinity;

    @Input()
    @HostBinding('class._bordered')
    bordered = false;

    @Output()
    readonly activeItemIndexChange = new EventEmitter<number | null>();

    @Output()
    readonly focusableItemIndexChange = new EventEmitter<number>();

    abstract readonly itemsConnectors?: QueryList<AbstractInteractiveGroupItemConnector>;

    protected abstract readonly focusableElements?: QueryList<ElementRef<HTMLElement>>;

    protected hoveredItemIndex: number | null = null;

    protected pressedItemIndex: number | null = null;

    private focusedChange$ = new Subject<boolean>();

    protected constructor(
        protected readonly changeDetectorRef: ChangeDetectorRef,
        protected readonly elementRef: ElementRef<HTMLElement>,
    ) {
        super();
        this.focusedChange$
            .pipe(
                startWith(false),
                distinctUntilChanged(),
                skip(1),
            )
            .subscribe(focused => {
                this.updateFocused(focused);
            });
    }

    get nativeFocusableElement(): NativeFocusableElement | null {
        if (!this.itemsConnectors || !this.focusableElements) {

            return null;
        }

        const itemsConnectors = getOriginalArrayFromQueryList(this.itemsConnectors);
        const focusableIndex = itemsConnectors.findIndex(
            // tslint:disable-next-line: no-shadowed-variable
            itemsConnectors => !itemsConnectors.disabled,
        );

        return focusableIndex === -1
            ? null
            : this.focusableElements.find((_item, index) => index === focusableIndex)!.nativeElement;
    }

    get focused(): boolean {
        return isNativeFocusedIn(this.elementRef.nativeElement);
    }

    get focusedZone(): number | null {
        return this.focused ? this.focusableItemIndex : null;
    }

    ngAfterContentInit(): void {
        if (!this.itemsConnectors) {

            return;
        }

        this.itemsConnectors.changes.subscribe(() => {
            this.changeDetectorRef.markForCheck();
        });
    }

    ngOnDestroy(): void {
        this.focusedChange$.complete();
    }

    @HostListener('focusout', ['$event'])
    onFocusOut({ relatedTarget, currentTarget }: EventWith<FocusEvent, Element>) {
        if (relatedTarget instanceof Element && !currentTarget.contains(relatedTarget)) {
            this.updateFocusedIndex(null);
        }
    }

    itemIsActive(itemIndex: number): boolean {
        return this.activeItemIndex === itemIndex;
    }

    itemIsFocusable(
        { disabled }: AbstractInteractiveGroupItemConnector,
        itemIndex: number,
    ): boolean {
        if (!this.itemsConnectors || !this.focusable || disabled) {

            return false;
        }

        if (itemIndex === this.focusableItemIndex) {

            return true;
        }

        return !this.itemsConnectors.some(
            // tslint:disable-next-line: no-shadowed-variable
            ({ disabled }, index) =>
                !disabled && (index < itemIndex || index === this.focusableItemIndex)
        );
    }

    itemIsHovered(
        { disabled }: AbstractInteractiveGroupItemConnector,
        itemIndex: number,
    ): boolean {
        return !disabled && this.hoveredItemIndex === itemIndex;
    }

    itemIsPressed(
        { disabled }: AbstractInteractiveGroupItemConnector,
        itemIndex: number,
    ): boolean {
        return !disabled && this.pressedItemIndex === itemIndex;
    }

    useLinkForItem({
        routerLink,
        disabled,
    }: AbstractInteractiveGroupItemConnector): boolean {
        return routerLink !== null && !disabled;
    }

    markForCheck() {
        this.changeDetectorRef.markForCheck();
    }

    protected scrollToElement(itemIndex: number, scrollabeElement: ElementRef) {
        if (!this.focusableElements) {

            return;
        }

        const scrollbar = scrollabeElement.nativeElement;
        const element = this.focusableElements.toArray()[itemIndex].nativeElement;
        const scrolledWidth = scrollbar.offsetWidth + scrollbar.scrollLeft;
        const elementWidth = element.offsetWidth;
        const elementX =
            scrollbar.scrollLeft +
            element.getBoundingClientRect().left -
            scrollbar.getBoundingClientRect().left;
        const elementDidNotFitRight = elementX + elementWidth > scrolledWidth;
        const elementDidNotFitLeft = scrollbar.scrollLeft > elementX;
        const deltaXRight = elementX + elementWidth - scrolledWidth;
        const deltaXLeft = elementX - scrollbar.scrollLeft;

        if (elementDidNotFitRight) {
            scrollbar.scrollLeft += deltaXRight;
        }

        if (elementDidNotFitLeft) {
            scrollbar.scrollLeft += deltaXLeft;
        }

    }

    protected userActivateItem(itemIndex: number) {
        this.updateFocusableItemIndex(itemIndex);
        this.updateActiveItemIndex(itemIndex);
    }

    protected updateActiveItemIndex(activeItemIndex: number | null) {
        if (this.activeItemIndex === activeItemIndex) {

            return;
        }

        this.activeItemIndex = activeItemIndex;
        this.activeItemIndexChange.emit(activeItemIndex);
    }

    protected updateHoveredItemIndex(hovered: boolean, itemIndex: number) {
        this.hoveredItemIndex = hovered ? itemIndex : null;
    }

    protected updatePressedItemIndex(pressed: boolean, itemIndex: number) {
        this.pressedItemIndex = pressed ? itemIndex : null;
    }

    protected updateFocusableItemIndex(focusableItemIndex: number) {
        if (!this.focusableElements || this.focusableItemIndex === focusableItemIndex) {

            return;
        }

        const element = this.focusableElements.find(
            (_item, index) => index === focusableItemIndex,
        );

        if (element) {
            setNativeFocused(element.nativeElement);
        }

        this.focusableItemIndex = focusableItemIndex;
        this.focusableItemIndexChange.emit(focusableItemIndex);
    }

    protected tryChangeActiveItemIndex(
        { disabled }: AbstractInteractiveGroupItemConnector,
        itemIndex: number,
    ) {
        if (disabled) {
            return;
        }

        this.userActivateItem(itemIndex);
    }

    protected updateFocusedIndex(itemIndex: number | null) {
        if (itemIndex === null) {
            this.focusedChange$.next(false);

            return;
        }

        this.updateFocusableItemIndex(itemIndex);
        this.focusedChange$.next(true);
    }

    protected moveFocusToPrevItem(itemIndex: number, event: Event) {
        this.processKeyDown(event);

        if (!this.itemsConnectors) {

            return;
        }

        const array: ReadonlyArray<AbstractInteractiveGroupItemConnector> = getOriginalArrayFromQueryList<AbstractInteractiveGroupItemConnector>(this.itemsConnectors);

        let newIndex = itemIndex - 1;

        do {
            if (newIndex < 0) {
                newIndex = array.length - 1;
            } else if (array[newIndex].disabled) {
                newIndex--;
            } else {
                this.updateFocusableItemIndex(newIndex);
                break;
            }
        } while (itemIndex !== newIndex);
    }

    protected moveFocusToNextItem(itemIndex: number, event: Event) {
        this.processKeyDown(event);

        if (!this.itemsConnectors) {

            return;
        }

        const array: ReadonlyArray<AbstractInteractiveGroupItemConnector> = getOriginalArrayFromQueryList<AbstractInteractiveGroupItemConnector>(this.itemsConnectors);

        let newIndex = itemIndex + 1;

        do {
            if (newIndex >= array.length) {
                newIndex = 0;
            } else if (array[newIndex].disabled) {
                newIndex++;
            } else {
                this.updateFocusableItemIndex(newIndex);
                break;
            }
        } while (itemIndex !== newIndex);
    }

    private processKeyDown(event: Event) {
        event.preventDefault();
        this.updateFocusVisible(true);
    }

}
