import { Component, OnInit, Input, ChangeDetectionStrategy, TemplateRef, ViewChild, ElementRef } from '@angular/core';

@Component({
    selector: 'mui-code-preview',
    templateUrl: './codePreview.tenplate.html',
    styleUrls: ['codePreview.style.less'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MuiCodePreviewComponent {
    @Input()
    code: string;

    @Input()
    language: string | string[];

    @ViewChild('codeContainer', { static: true })
    codeContainer: ElementRef<HTMLElement>;

    get languages(): string[] {
        if (Array.isArray(this.language)) {
            return this.language;
        }

        return [
            this.language
        ];
    }

    public onClick(): void {
        const text = this.codeContainer.nativeElement.innerText;

        document.addEventListener('copy', (event: ClipboardEvent) => {
            event.clipboardData.setData('text/plain', text);
            event.preventDefault();
        }, { once: true });
        document.execCommand('copy');
    }
}
