import { PreviewComponentClass } from '../../types/preview-component-class.type';
import { Component, ChangeDetectionStrategy, OnChanges, Input, ComponentRef, ViewContainerRef, ComponentFactoryResolver, ComponentFactory, SimpleChanges } from '@angular/core';
import { PreviewComponentInterface } from '../../interfaces/preview-component.interface';

@Component({
    selector: 'app-preview-component-builder',
    template: '',
    styleUrls: ['./preview-component-builder.component.less'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PreviewComponentBuilderComponent implements OnChanges {

    @Input()
    component: PreviewComponentClass<Component>;

    @Input()
    componentProperties: { [key: string]: any };

    private componentRef: ComponentRef<PreviewComponentInterface<Component>>;

    private previousComponentExists: boolean = false;

    constructor(
        private viewContainerRef: ViewContainerRef,
        private componentFactoryResolver: ComponentFactoryResolver,
    ) { }

    ngOnChanges(simpleChanges: SimpleChanges): void {
        if ('component' in simpleChanges) {
            this.createComponent();
        }

        if ('componentProperties' in simpleChanges) {
            this.componentRef.instance.setProperties(this.componentProperties);
        }
    }

    private createComponent(): void {
        this.destroyPreviousComponent();

        this.previousComponentExists = true;

        const componentFactory: ComponentFactory<PreviewComponentInterface<Component>> = this.componentFactoryResolver.resolveComponentFactory(this.component);

        this.componentRef = this.viewContainerRef.createComponent(componentFactory)
    }

    private destroyPreviousComponent(): void {
        if (this.previousComponentExists) {
            this.viewContainerRef.remove(0);
        }
    }

}
