import { Directive, Input, HostBinding } from '@angular/core';

@Directive({
    // tslint:disable-next-line: directive-selector
    selector: '[focusable]',
})
export class FocusableDirective {
    @Input('focusable')
    focusable = true;

    @HostBinding('tabIndex')
    get tabIndex(): number {
        return this.focusable ? 0 : -1;
    }
}
