import { Subscriber, Observable, TeardownLogic, OperatorFunction, Operator } from 'rxjs';

class IsAliveSubscriber extends Subscriber<boolean> {
    private timeoutId: any;

    constructor(
        destination: Subscriber<boolean>,
        private readonly lifespan: number
    ) {
        super(destination);
    }

    protected _next(_value: any) {
        if (!this.timeoutId) {
            this.destination.next!(true);
        } else {
            clearTimeout(this.timeoutId);
        }

        this.timeoutId = setTimeout(() => {
            this.timeoutId = null;
            this.destination.next!(false);
        }, this.lifespan);
    }
}

export class IsAliveOperator implements Operator<any, boolean> {
    constructor(private readonly lifespan: number) { }

    call(subscriber: Subscriber<boolean>, source$: Observable<any>): TeardownLogic {
        return source$.subscribe(new IsAliveSubscriber(subscriber, this.lifespan));
    }
}

export function isAlive(lifespan: number = 0): OperatorFunction<any, boolean> {
    return source$ => source$.lift(new IsAliveOperator(lifespan));
}
