import {
    startWith,
    shareReplay,
    take,
    filter,
    ignoreElements,
    repeat,
    mapTo,
    distinctUntilChanged,
    switchMapTo,
    withLatestFrom
} from 'rxjs/operators';
import { Observable, concat, merge } from 'rxjs';
import { blurFrom, mouseUpFrom, mouseDownFrom, focusFrom } from './fromAliases';
import { isAlive } from './isAlive';
import { isNativeFocused } from '../utils/focus/isNativeFocused';

let documentMouseUpIsAlive$: Observable<boolean>;
let documentMouseDownIsAlive$: Observable<boolean>;

export function focusVisibleObservable(element: Element): Observable<boolean> {
    const elementBlur$ = blurFrom(element);
    const { ownerDocument } = element;

    if (!ownerDocument) {
        throw new Error('element does not have ownerDocument');
    }

    if (!documentMouseDownIsAlive$ || !documentMouseUpIsAlive$) {
        documentMouseUpIsAlive$ = mouseUpFrom(ownerDocument, { capture: true }).pipe(
            isAlive(),
            startWith(false),
            shareReplay(1),
        );
        documentMouseDownIsAlive$ = mouseDownFrom(ownerDocument, { capture: true }).pipe(
            isAlive(),
            startWith(false),
            shareReplay(1),
        );
    }

    return merge(
        concat(
            focusFrom(element).pipe(take(1)),
            elementBlur$.pipe(
                filter(() => !isNativeFocused(element)),
                take(1),
                ignoreElements(),
            ),
        ).pipe(
            repeat(),
            withLatestFrom(
                documentMouseDownIsAlive$,
                documentMouseUpIsAlive$,
                (_event, elementActual, documentActual) =>
                    elementActual || documentActual,
            ),
            filter(isUserFocus => !isUserFocus),
        ),
    ).pipe(
        switchMapTo(
            elementBlur$.pipe(
                mapTo(false),
                take(1),
                startWith(true),
            ),
        ),
        distinctUntilChanged(),
    );

}
